.table {
  margin: 20px 0;
}

.table-header {
  display: -ms-flexbox;
  display: flex;
  line-height: 24px;
  font-size: 16px;
  padding: 0 10px;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.table-empty {
  margin: 200px;
  text-align: center;
  font-size: 16px;
}

.table-row {
  display: -ms-flexbox;
  display: flex;
  line-height: 24px;
  white-space: nowrap;
  /*margin: 10px 0;*/
  /*padding: 10px;*/
  -ms-flex-align: center;
      align-items: center;
  background: #ffffff;
  border-top: 1px solid #e3e3e3;
}

.table-header > span {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  padding: 0 5px;
}

.table-row > span {
  /*overflow: hidden;*/
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  padding: 0 5px;
}

.arrow {
	height: 10px;
  width: 10px;
	border: 1px solid #00A7B2;
	border-width: 2px 2px 0 0;
  float: right;
}
.arrow-up {
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
  margin: 15px 10px 0 0;
}
.arrow-down {
  -webkit-transform: rotate(135deg);
      -ms-transform: rotate(135deg);
          transform: rotate(135deg);
  margin: 10px 10px 0 0;
}



